.DataLibraryMap {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.DataLibraryMap,
.DataLibraryMap .leaflet-zoom-animated {
  background: #2E3C43 !important; }

.DataLibrary-header {
  height: 450px; }
  .DataLibrary-header .DataLibraryMap {
    pointer-events: none; }
  .DataLibrary-header.is-active .DataLibraryMap {
    pointer-events: auto; }
  .DataLibrary-header.is-active .DataLibrary-gradient {
    display: block; }

.DataLibrary-gradient {
  display: none;
  position: relative;
  z-index: 9;
  width: 100%;
  height: 100px;
  margin-top: -70px;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0) 100%); }
