.Header {
  position: relative; }
  .Header .Header-title {
    height: auto; }

.Header--dark {
  background: #2E3C43; }

.Header--tall {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-height: 450px;
  overflow: hidden; }

.Header-title {
  display: flex;
  flex-grow: 1;
  align-items: center;
  margin: 0 auto;
  padding-top: 50px;
  padding-bottom: 50px; }
  .Header-title.is-hidden {
    display: none; }

.Header-innerTitle {
  width: 100%;
  max-width: 760px; }

.Header-inner {
  z-index: 10;
  box-sizing: border-box;
  width: 100%;
  max-width: 940px;
  height: 80px; }

.Header-footer {
  position: absolute;
  bottom: 0;
  max-width: 100%; }
  .Header-footer.is-hidden {
    display: none; }
