/* FONT FACE */
.CountrySelector {
  margin-right: auto;
  margin-bottom: 30px;
  margin-left: auto;
  text-align: right; }
  .CountrySelector.is-hidden {
    display: none; }

.CountrySelector-button {
  background: url("/assets/1.0.0-assets.136/images/layout/data_library/country_selector.png") no-repeat center center;
  display: inline-block;
  width: 40px;
  height: 40px;
  margin-left: 12px;
  border: 1px solid #bbbbbb;
  border-radius: 4px;
  outline: none;
  text-align: center;
  vertical-align: middle; }
  @media (-webkit-min-device-pixel-ratio: 1.3), (min-resolution: 1.3dppx) {
    .CountrySelector-button {
      /* on retina, use image that's scaled by 2 */
      background: url("/assets/1.0.0-assets.136/images/layout/data_library/country_selector@2x.png");
      background-position: center center;
      background-size: 40px 40px; } }

.CountrySelector-button:hover {
  border-color: #888888; }
  .CountrySelector-button:hover .CountrySelector-icon {
    color: #888888; }

.CountrySelector-text {
  display: inline-block;
  color: #FFF;
  font-size: 15px;
  line-height: 40px; }

.CountrySelector-text,
.CountrySelector-button {
  pointer-events: auto; }

.CountrySelector-inner {
  pointer-events: none; }
